/* ##### 3.0 Wellcome Area CSS ##### */
.hero-section{
    position: relative;
    min-height: 700px;
    /*background: url('../../../assets/img/bg-img/header2.jpg') no-repeat bottom center;*/
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.moving-bg{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 500em;
    height: 70%;
    opacity: .1;
    visibility: inherit;
    background: transparent url(../../static/media/crypto.eeb7805d.png) repeat-x scroll 0 100%;
    -webkit-animation: cloudwash 60s linear infinite;
    animation: cloudwash 60s linear infinite;
}
@-webkit-keyframes cloudwash {
    0% {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); 
      }
    100% {
      -webkit-transform: translate3d(-50%, 0, 0);
      transform: translate3d(-50%, 0, 0); 
      } 
  }
  
  @keyframes cloudwash {
    0% {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); }
    100% {
      -webkit-transform: translate3d(-50%, 0, 0);
      transform: translate3d(-50%, 0, 0); } 
  }
@media(max-width: 992px){
    .hero-section.ai3,
    .hero-section.ai4{
        background-position: top left;
    }
}
.hero-section.ai5{
    background: url(../../static/media/ai5.9fb6a3a4.jpg) no-repeat top left;
