}
.hero-section.overlayed{
    /*background: url('../../../assets/img/bg-img/h-sec.jpg') no-repeat bottom center;*/
    background-size: cover;
}
.overlayed:before{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background: #13287e;
    opacity: .88;
    z-index:0;
}
.overlayed .video-icon{
    position: relative;
    left: 50%;
    margin-bottom: 30px;
}
.overlayed .video-icon:before {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    height: 110px;
    width: 110px;
    background: #42d897;
    z-index: -1;
    border-radius: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    -webkit-animation: pulse-anim 1500ms ease-out infinite;
            animation: pulse-anim 1500ms ease-out infinite;
}
@keyframes pulse-anim {
  0% {
    -webkit-transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1);
            transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1);
    opacity: 1;
    }
  100% {
    -webkit-transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
            transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
    opacity: 0;
    }
}
@-webkit-keyframes pulse-anim {
  0% {
    -webkit-transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1);
            transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1);
    opacity: 1;
    }
  100% {
    -webkit-transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
            transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
    opacity: 0;
    }
}

